class Social(
    val name: String,
    val tag: String,
    val link: String
)

private val socials = listOf(
    Social(
        "Instagram",
        "@IGL_esportsleagues",
        "https://www.instagram.com/igl_esportsleagues/"
    ),
    Social(
        "TikTok",
        "@IGL_esportsleagues",
        "http://tiktok.com/@igl_esportsleagues"
    ),
    Social(
        "Twitter",
        "IGL_Esports",
        "https://x.com/IGL_Esports"
    ),
)

@OptIn(ExperimentalMaterial3Api::class)
@Composable
fun App(
    logEvent: ((name: String, parameters: Map<String, Any>?) -> Unit)? = null
) {
    AppTheme {
        val gemFont = FontFamily(
            Font(Res.font.GemunuLibre_Bold, FontWeight.Normal, FontStyle.Normal)
        )
        Scaffold(
            modifier = Modifier.fillMaxSize(),
            topBar = {
                TopAppBar(
                    colors = TopAppBarDefaults.topAppBarColors().copy(containerColor = MaterialTheme.colorScheme.primaryContainer),
                    title = {},
                    actions = {
                        TextButton(
                            onClick = {
                                logEvent?.invoke("create_account_clicked", null)
                            }
                        ) {
                            Text(
                                fontFamily = gemFont,
                                text = "Create Account",
                                fontSize = 24.sp,
                                color = MaterialTheme.colorScheme.secondary
                            )
                        }
                    }
                )
            }
        ) { padding ->
            Column(
                modifier = Modifier
                    .padding(padding)
                    .fillMaxWidth()
                    .verticalScroll(rememberScrollState()),
                horizontalAlignment = Alignment.CenterHorizontally
            ) {

                Image(
                    painter = painterResource(Res.drawable.IGL_Logo_Final_Outline_Email),
                    contentDescription = "Logo",
                    modifier = Modifier.size(300.dp).padding(16.dp)
                )
                val gemFont = FontFamily(
                    Font(Res.font.GemunuLibre_Regular, FontWeight.Normal, FontStyle.Normal)
                )
                Text(
                    text = "Compete. Connect. Content.",
                    fontFamily = gemFont,
                    style = MaterialTheme.typography.headlineLarge,
                    modifier = Modifier.padding(16.dp),
                    fontSize = 80.sp,
                    textAlign = TextAlign.Center
                )
                Text(
                    text = "The app that is changing the way Esports fans interact with the games they love and find new friends who share their passion.",
                    fontFamily = gemFont,
                    style = MaterialTheme.typography.titleLarge,
                    modifier = Modifier.padding(16.dp),
                    fontSize = 30.sp,
                    textAlign = TextAlign.Center
                )
                Spacer(Modifier.height(100.dp))
                SurveyMessage()
                Spacer(Modifier.height(100.dp))
                Column(
                    modifier = Modifier.fillMaxWidth(),
                    horizontalAlignment = Alignment.CenterHorizontally,
                    verticalArrangement = Arrangement.spacedBy(2.dp)
                ) {
                    Text(
                        "Socials",
                        fontFamily = gemFont,
                        style = MaterialTheme.typography.headlineMedium,
                        modifier = Modifier.padding(16.dp),
                        textAlign = TextAlign.Center
                    )
                    for (social in socials) {
                        SocialLink(social)
                    }
                }
                Spacer(Modifier.height(100.dp))
            }
        }
    }
}

@Composable
private fun SurveyMessage() {
    val gemFont = FontFamily(
        Font(Res.font.GemunuLibre_Regular, FontWeight.Normal, FontStyle.Normal)
    )
    val uriHandler = LocalUriHandler.current

    Text(
        buildAnnotatedString {
            withStyle(
                style = SpanStyle(
                    fontSize = 25.sp,
                    fontFamily = gemFont,
                    color = MaterialTheme.colorScheme.secondary
                )
            ) {
                append("If you love esports and want to help it grow, please consider taking 5 minutes to ")
            }

            withLink(
                LinkAnnotation.Clickable(
                    tag = "clickable",
                    styles = TextLinkStyles(
                        style = SpanStyle(
                            fontSize = 25.sp,
                            fontFamily = gemFont,
                            textDecoration = TextDecoration.Underline,
                            color = MaterialTheme.colorScheme.primary
                        ),
                    )
                ) {
                    uriHandler.openUri("https://docs.google.com/forms/d/e/1FAIpQLScuu-RRRQFIauqrng7CkQDGYZjAfC8LsZd2Wb5-Sa3aoW8NZg/viewform")
                }
            ) {
                append("fill out a survey")
            }

            withStyle(
                style = SpanStyle(
                    fontSize = 25.sp,
                    fontFamily = gemFont,
                    color = MaterialTheme.colorScheme.secondary
                )
            ) {
                append(" on the platform we are currently building.")
            }
            withStyle(
                style = SpanStyle(
                    fontSize = 25.sp,
                    fontFamily = gemFont,
                    color = MaterialTheme.colorScheme.secondary
                )
            ) {
                append("\nThank you.")
            }
        },
        textAlign = TextAlign.Center
    )
}

@Composable
private fun SocialLink(
    social: Social
) {
    val gemFont = FontFamily(
        Font(Res.font.GemunuLibre_Regular, FontWeight.Normal, FontStyle.Normal)
    )
    val uriHandler = LocalUriHandler.current
    Row(
        modifier = Modifier.padding(vertical = 4.dp),
        horizontalArrangement = Arrangement.spacedBy(8.dp),
        verticalAlignment = Alignment.CenterVertically
    ) {
        Text(
            text = social.name.plus(" :"),
            fontFamily = gemFont,
            style = MaterialTheme.typography.titleMedium,
            color = MaterialTheme.colorScheme.secondary
        )
        Text(
            buildAnnotatedString {
                withLink(
                    LinkAnnotation.Clickable(
                        tag = "clickable",
                        styles = TextLinkStyles(
                            style = SpanStyle(
                                fontFamily = gemFont,
                                textDecoration = TextDecoration.Underline,
                                color = MaterialTheme.colorScheme.primary
                            ),
                        )
                    ) {
                        // log some metrics
                        uriHandler.openUri(social.link)
                    }
                ) {
                    append(social.tag)
                }
            },
            textAlign = TextAlign.Center
        )
    }
}

@Composable
private fun AppTheme(
    darkTheme: Boolean = isSystemInDarkTheme(),
    content: @Composable() () -> Unit
) {
    val colorScheme = when {
        darkTheme -> darkScheme
        else -> lightScheme
    }

    MaterialTheme(
        colorScheme = colorScheme,
        content = content
    )
}