import firebase.FirebaseWrapper
import kotlinx.browser.document
import kotlinx.datetime.Clock.System.now
import kotlinx.datetime.TimeZone
import kotlinx.datetime.toLocalDateTime

@OptIn(ExperimentalComposeUiApi::class)
fun main() {
    Firebase.initialize(
        options = FirebaseOptions(
            apiKey = "AIzaSyBCAEf4Rcn_WZLb9L0p_b4CicpO9PWaRlI",
            authDomain = "integrated-gaming-leagues.firebaseapp.com",
            projectId = "integrated-gaming-leagues",
            storageBucket = "integrated-gaming-leagues.firebasestorage.app",
            gcmSenderId = "50304558274",
            applicationId = "1:50304558274:web:4a3bdad0cfb2b6893d475b"
        )
    )
    val analytics = Firebase.analytics
    val body = document.body ?: return
    ComposeViewport(body) {
        App { name, parameters ->
            val formattedTime = now().toLocalDateTime(TimeZone.currentSystemDefault())
            analytics.logEvent(name, parameters?.plus("date" to formattedTime.toString()))
        }
    }
}